<template>
  <div class="chatTop">
    <div class="chatContainer">
      <aside>
        <div class="aside-head">
          <h2>Rooms</h2>
          <p>
            <span id="mark">●</span>
            {{ loginUser }}さん
          </p>
        </div>
        <div class="aside-body">
          <section>
            <div></div>
          </section>
        </div>
      </aside>
      <main class="chat">
        <message v-bind:chatLists="this.chatLists"></message>
        <textForm v-on:submit="postMessage"></textForm>
      </main>
    </div>
    <footer>
      <div class="chatFooter" @click="sendToRoom">Room一覧に戻る</div>
    </footer>
  </div>
</template>

<script>
import message from "./chat/message.vue";
import textForm from "./chat/textForm.vue";
import firebase from "firebase";
import mixin from "../mixins.js";

export default {
  name: "chat",
  props: [],
  components: {
    message: message,
    textForm: textForm,
  },

  data: function () {
    return {
      chatLists: [],
      loginUser: this.$route.query.loginUser,
      profileImg: this.$route.query.profileImg,
      roomId: this.$route.query.roomId,
      tableName: "chat_" + this.$route.query.roomId,
    };
  },

  mixins: [mixin],

  mounted: function () {
    //チャットを取得
    this.getMessagesFromDB();

    //TODO 非同期処理の問題
    setTimeout(() => {
      this.scrollBottom();
    }, 600);
  },

  methods: {
    /**
     *メッセージを投稿する
     *@param input 投稿メッセージ
     */
    postMessage: function (input) {
      //投稿メッセージのチェック処理
      if (input.length > 200 || input.length === 0) {
        return;
      }

      //Cloud Firestoreへ接続しメッセージを登録
      this.sendMessageToDB(input);

      //メッセージを再取得する
      this.getMessagesFromDB();

      //roomの最終更新日時を更新する
      this.updateDate();

      //下スクロール
      this.scrollBottom();
    },

    /**
     * Cloud Firestoreへ接続しメッセージを登録する
     *@param input 投稿メッセージ
     */
    sendMessageToDB: function (input) {
      //messageIdを採番
      //ランダムな8桁英数 + 通番
      const id = this.getRandomString() + (this.chatLists.length + 1);
      const name = this.loginUser;
      const img = this.profileImg;

      //登録するmessageオブジェクトを作成
      let message = {
        id: id,
        name: name,
        img: img,
        message: input,
        date: this.getCurrentDate(),
      };

      //Cloud Firestoreへ接続
      firebase
        .firestore()
        .collection("rooms")
        .doc(this.roomId)
        .collection("chat")
        .doc(id)
        .set({ message })
        .then((doc) => {
          console.log(`${doc.id} added!`);
        })
        .catch((error) => {
          console.log(error);
        });
    },

    /**
     *chatを取得する
     *Cloud Firestoreへ接続
     */
    getMessagesFromDB: function () {
      let chatLists = [];

      //Cloud FireStoreへ接続しchatを取得
      firebase
        .firestore()
        .collection("rooms")
        .doc(this.roomId)
        .collection("chat")
        .onSnapshot((snap) => {
          snap.docChanges().forEach((change) => {
            const message = change.doc.data();
            chatLists.push(message.message);
          });
        });
      //TODO
      //メッセージを最終更新日でソート
      setTimeout(() => {
        this.chatLists = chatLists.sort(function (a, b) {
          return a.date < b.date ? -1 : 1;
        });
      }, 800);
    },

    /**
     * 下スクロールする
     */
    scrollBottom: function () {
      // this.$nextTick(() => {
      setTimeout(() => {
        window.scrollTo(0, document.querySelector(".chat").clientHeight);
      }, 300);
    },

    /**
     *roomの最終更新日を現在日時で更新する
     */
    updateDate: function () {
      firebase
        .firestore()
        .collection("rooms")
        .doc(this.roomId)
        .update({
          // last_modified: this.chatLists[this.chatLists.length - 1].date,
          last_modified: this.getCurrentDate(),
        })
        .then(() => {
          console.log(`${this.inputRoomName}roomを更新しました`);
        })
        .catch((error) => {
          console.log(error);
        });
    },

    /**
     * room一覧へ戻る
     * 引継ぎ情報：userId,loginUser,profileImg
     */
    sendToRoom: function () {
      this.$router.push({
        path: "/room",
        query: {
          userId: this.$route.query.userId,
          loginUser: this.$route.query.loginUser,
          profileImg: this.$route.query.profileImg,
        },
      });
    },
  },
};
</script>

