<template>
  <section id="RoomForm">
    <form @submit.prevent="createRoom">
      <div>
        <div v-show="errorMsg !== ''" style="color: red">{{ errorMsg }}</div>
        <label type="text">*Room Name：</label>
        <input type="text" class="form-control" id="text" v-model="inputRoomName" />
        <br />
        <br />
        <p>Private or Open ?：</p>
        <label>
          <p style="display:inline-block; width:100px;">
            <input type="radio" v-model="isPrivateRoom" value="true" />:Private
          </p>
        </label>
        <label>
          <p style="display:inline-block;">
            <input type="radio" v-model="isPrivateRoom" value="false" />:Open
          </p>
        </label>
        <br />
        <label type="text">*Room Key：</label>
        <input
          type="password"
          class="form-control"
          v-bind:disabled="isPrivateRoom === 'false'"
          v-model="inputRoomKey"
        />
        <br />
        <br />
        <label type="textarea">introduction(max 200 characters)：</label>
        <span class="left-space" v-show="contents.length <= 200">{{contents.length}}characters</span>

        <textarea
          type="textarea"
          class="form-control introduction"
          v-model="contents"
          maxlength="200"
          rows="3"
        ></textarea>
      </div>
      <br />
      <button type="submit" class="btn btn-info">作成</button>
    </form>
  </section>
</template>

<script>
import firebase from "firebase";
import mixin from "../../mixins.js";

export default {
  name: "roomCreation",
  mixins: [mixin],
  props: ["errorMsg", "loginUser", "userId"],

  data: function () {
    return {
      isPrivateRoom: true,
      inputRoomName: "",
      inputRoomKey: "",
      contents: "",
    };
  },

  methods: {
    /**
     *ルームを作成する
     *Cloud Firestoreへ接続
     */
    createRoom: function () {
      this.errorMsg = "";

      //入力チェック
      if (!this.checkRoomForm()) {
        return;
      }

      //8桁のランダムなroomIdを採番
      const roomId = this.getRandomString();

      //introductionに入力がない場合はデフォルト説明を設定
      if (this.getLen(this.contents) === 0) {
        this.contents = `${this.inputRoomName}の部屋です。よろしくお願いします。`;
      }

      //Cloud Firestoreへ登録
      firebase
        .firestore()
        .collection("rooms")
        .doc(roomId)
        .set({
          contents: this.contents,
          roomName: this.inputRoomName,
          roomId: roomId,
          roomKey: this.inputRoomKey,
          founder: this.loginUser,
          founderUID: this.userId,
          creation_date: this.getCurrentDate(),
          last_modified: this.getCurrentDate(),
        })
        .then(() => {
          alert(`${this.inputRoomName}roomを作成しました`);
        })
        .catch((error) => {
          console.log(error);
        });

      //フォームを初期化
      this.inputRoomKey = "";
      this.isPrivateRoom = true;
      this.contents = "";
      this.inputRoomName = "";
    },

    /**
     * ルーム作成フォームのチェック処理を行う
     * @return {boolean}
     */
    checkRoomForm: function () {
      //チェック処理 TODOもっと厳密にする必要がある
      if (this.inputRoomName === "") {
        this.errorMsg = "RoomNameが入力されていません";
        return false;
      }

      if (this.getLen(this.inputRoomName) > 40) {
        this.errorMsg = "RoomNameは40文字以下で入力してください";
        return false;
      }

      //private room選択時のみ
      if (this.isPrivateRoom === true && String(this.inputRoomKey).length < 4) {
        this.errorMsg = "4文字以上のRoomKeyを入力してください";
        return false;
      }

      if (this.getLen(this.inputRoomKey) > 20) {
        this.errorMsg = "inputRoomKeyは20文字以下で設定してください";
        return false;
      }

      if (this.getLen(this.contents) > 200) {
        this.errorMsg = "introductionは200文字以下で入力してください";
        return false;
      }
      return true;
    },
  },
};
</script>

