export default {
  methods: {
    /**
     * 現在日時をyy/mm/dd hh mm ss形式で返す
     * @return yy/mm/dd hh mm ss
     */
    getCurrentDate: function() {
      const dt = new Date();
      const year = dt.getFullYear();
      const month = ('00' + (dt.getMonth() + 1)).slice(-2);
      const date = ('00' + dt.getDate()).slice(-2);
      const hour = ('00' + dt.getHours()).slice(-2);
      const minite = ('00' + dt.getMinutes()).slice(-2);
      const second = ('00' + dt.getSeconds()).slice(-2);
      return (
        year +
        '/' +
        month +
        '/' +
        date +
        ' ' +
        hour +
        ':' +
        minite +
        ':' +
        second
      );
    },

    // getTimeStampDate: function() {
    //   let ts = this.getCurrentDate();
    //   print(Date.parse(ts.replace(/-/g, '/')) / 1000);
    // },

    /**
     * 英数字混合の8桁のランダム文字列を生成
     * @return randomStr
     */
    getRandomString: function() {
      // 生成する文字列の長さ
      const l = 8;

      // 生成する文字列に含める文字セット
      const c = 'abcdefghijklmnopqrstuvwxyz0123456789';

      const cl = c.length;
      let randomStr = '';
      for (let i = 0; i < l; i++) {
        randomStr += c[Math.floor(Math.random() * cl)];
      }

      return randomStr;
    },

    /**
     * 引数で受け取った文字列の長さを返す
     * 半角1文字、全角2文字でカウント
     * @param str
     * @return len
     */
    getLen: function(str) {
      let len = 0;
      for (let i = 0; i < str.length; i++) {
        let chr = str.charCodeAt(i);
        if (
          (chr >= 0x00 && chr < 0x81) ||
          chr === 0xf8f0 ||
          (chr >= 0xff61 && chr < 0xffa0) ||
          (chr >= 0xf8f1 && chr < 0xf8f4)
        ) {
          //半角文字の場合は1を加算
          len += 1;
        } else {
          //それ以外の文字の場合は2を加算
          len += 2;
        }
      }
      //結果を返す
      return len;
    },
  },
};
