  <template>
  <div>
    <message v-bind:chatLists="this.chatLists"></message>
    <textForm v-on:submit="postMessage"></textForm>
  </div>
</template>

<script>
import message from "./message.vue";
import textForm from "./textForm.vue";
import mixin from "../../mixins.js";

export default {
  name: "chat",
  props: ["loginUser", "profileImg"],
  components: {
    message: message,
    textForm: textForm,
  },

  data: function () {
    return {
      chatLists: [
        {
          id: "0001",
          name: "chang-kawauso",
          img: require("@/assets/img/otter.jpg"),
          message: "ん、何？質問？聞くよ！！！",
          date: this.getCurrentDate(),
        },
      ],
      replyMsgMap: new Map(),
    };
  },

  mixins: [mixin],
  mounted: function () {
    //キーワードと返答を設定する
    this.replyMsgMap.set("うんこ", "くさいくさい、うんこくさい！！！");
    this.replyMsgMap.set("考え方", "やっぱりさー、考え方の問題だよねー");
    this.replyMsgMap.set("かわうそ", "僕は二ホンカワウソです");
    this.replyMsgMap.set("kick", "ちゃん・かわうそ、キー！！！");
    this.replyMsgMap.set("なかじま", "なかじまってさー、使えないよねー");
    this.replyMsgMap.set("kimochi", "わかるわかる、よくわかるその気持ち");
  },

  methods: {
    /**
     * メッセージを投稿する
     * 永続化はしない
     */
    postMessage: function (input) {
      if (input.length > 200 || input.length === 0) {
        return;
      }

      const id = this.chatLists.length + 1;
      const name = this.loginUser;
      const img = this.profileImg;

      //メッセージを追加
      this.chatLists.push({
        id: id,
        name: name,
        img: img,
        message: input,
        date: this.getCurrentDate(),
      });

      setTimeout(() => {
        this.scrollBottom();
      }, 800);
    },

    /**
     * ユーザーの投稿に対して返答する
     * @param key 投稿メッセージに応じて設定されたキーワード
     */
    replyMsg: function (key) {
      const id = this.chatLists.length + 1;
      const name = "chang-kawauso";

      //inputに応じた返答をmapから取得する
      const reply = this.replyMsgMap.get(key);

      //返答メッセージを追加
      this.chatLists.push({
        id: id,
        name: name,
        img: require("@/assets/img/otter.jpg"),
        message: reply,
        date: this.getCurrentDate(),
      });

      this.scrollBottom();
    },

    /**
     * 下スクロール
     */
    scrollBottom: function () {
      window.scrollTo(0, document.body.clientHeight);
    },
  },

  watch: {
    chatLists: function () {
      //最新のユーザーの投稿メッセージを取得
      const lastMsg = this.chatLists[this.chatLists.length - 1].message;
      const name = this.chatLists[this.chatLists.length - 1].name;

      //ユーザーの投稿以外には返答しない
      if (name === "chang-kawauso") {
        return;
      }

      //投稿メッセージに含まれるワードをもとにキーを設定し、
      //mapから返答を取得する
      setTimeout(() => {
        if (
          lastMsg.indexOf("きらい") != -1 ||
          lastMsg.indexOf("カス") != -1 ||
          lastMsg.indexOf("うざい") != -1
        ) {
          this.replyMsg("考え方");
          return;
        }
        if (
          lastMsg.indexOf("うんこ") != -1 ||
          lastMsg.indexOf("unko") != -1 ||
          lastMsg.indexOf("ぶりぶり") != -1
        ) {
          this.replyMsg("うんこ");
          return;
        }
        if (
          lastMsg.indexOf("かわうそ") != -1 ||
          lastMsg.indexOf("カワウソ") != -1 ||
          lastMsg.indexOf("kawauso") != -1 ||
          lastMsg.indexOf("皮嘘") != -1
        ) {
          this.replyMsg("かわうそ");
          return;
        }
        if (lastMsg.indexOf("chang") != -1 || lastMsg.indexOf("ちゃん") != -1) {
          this.replyMsg("kick");
          return;
        }
        if (lastMsg.indexOf("犬") != -1 || lastMsg.indexOf("なかじま") != -1) {
          this.replyMsg("なかじま");
          return;
        }
        this.replyMsg("kimochi");
      }, 2000);

      setTimeout(() => {
        this.scrollBottom();
      }, 2800);
    },
  },
};
</script>

