<template>
  <div class="container mt-5">
    <h2>新規登録</h2>
    <br />
    <span class="errorMsg">{{errorMsg}}</span>
    <br />
    <div class="row">
      <div class="col-sm-8">
        <form @submit.prevent="signUp">
          <div class="form-group">
            <label for="displayName">ニックネーム：</label>

            <input type="text" class="form-control" placeholder="ちゃん・かわうそ" v-model="username" />
          </div>

          <div class="form-group">
            <label for="email">メールアドレス：</label>
            <input type="email" class="form-control" id="email" v-model="email" />
          </div>
          <div class="form-group">
            <label type="password">パスワード：</label>
            <input type="password" class="form-control" id="password" v-model="password" />
          </div>
          <div class="form-group">
            <label type="password">パスワード(確認)：</label>
            <input type="password" class="form-control" id="password" v-model="password2" />
          </div>
          <button type="submit" class="btn btn-info">登録</button>
        </form>
        <p>
          <router-link to="/">サインインはこちら</router-link>
        </p>
      </div>
    </div>
  </div>
</template>

<script>
import firebase from "firebase";

export default {
  name: "singUp",
  props: [],
  data: function () {
    return {
      email: "",
      password: "",
      password2: "",
      username: "",
      isAuthenticated: false,
      errorMsg: "",
    };
  },

  mounted: function () {
    this.errorMsg = "";
  },

  methods: {
    /**
     * Cloud Firestoreへ接続しユーザー情報を登録する
     * 登録内容：email password displayName photoURL
     */
    signUp: function () {
      //入力チェック
      if (!this.checkForm()) {
        return;
      }

      //Cloud Firestoreへ登録する
      firebase
        .auth()
        .createUserWithEmailAndPassword(this.email, this.password)
        .then((result) => {
          result.user.updateProfile({
            displayName: this.username,
            photoURL: "/img/moria.jpg",
          });
          alert("アカウントを作成しました");
          this.$router.push("/");
        })
        .catch((error) => {
          this.errorMsg = `システムエラー：${error.message}`;
        });
    },

    /**
     * フォームチェックを行う
     * email passwordはfirebaseで実装されているチェックも実施
     * @return {boolean}
     */
    checkForm: function () {
      if (this.username.length < 2) {
        this.errorMsg = "名前は2文字以上で入力してください";
        return false;
      }

      if (this.email === "") {
        return false;
      }

      if (this.password.length < 6) {
        this.errorMsg = "パスワードは6文字以上で入力してください";
        return false;
      }

      if (this.password !== this.password2) {
        this.errorMsg = "パスワードが一致しません";
        return false;
      }

      return true;
    },
  },
};
</script>

<style scoped>
.errorMsg {
  color: red;
}
</style>
