<template>
  <header>
    <h1>Otter chat</h1>
    <div class="singOut" @click="signOut">サインアウト</div>
  </header>
</template>

<script>
import firebase from "firebase";

export default {
  name: "headerComp",
  props: {},
  data: function () {
    return {};
  },

  methods: {
    /**
     * サインアウトする
     */
    signOut: function () {
      firebase
        .auth()
        .signOut()
        .then(() => {
          this.$router.push("/");
        });
    },
  },
};
</script>

