<template>
  <div class="container mt-5">
    <h2>ログイン画面</h2>
    <div class="row">
      <div class="col-sm-8">
        <form @submit.prevent="signIn">
          <div class="form-group">
            <label for="email">メールアドレス：</label>
            <input type="email" class="form-control" id="email" v-model="email" />
          </div>
          <div class="form-group">
            <label type="password">パスワード：</label>
            <input type="password" class="form-control" id="password" v-model="password" />
          </div>
          <button type="submit" class="btn btn-info">ログイン</button>
        </form>

        <p>
          既にアカウントを持っていますか？
          <br />
          <router-link to="/signUp">サインアップはこちら</router-link>
        </p>
      </div>
    </div>
  </div>
</template>

<script>
import firebase from "firebase";

export default {
  name: "signIn",
  props: [],
  data: function () {
    return {
      email: "",
      password: "",
      isAuthenticated: false,
    };
  },

  methods: {
    /**
     * Cloud Firestoreへ接続しログイン認証処理を行う
     * 成功した場合roomに画面遷移する
     */
    signIn: function () {
      firebase
        .auth()
        .signInWithEmailAndPassword(this.email, this.password)
        .then((result) => {
          this.$router.push({
            path: "/room",
            query: {
              userId: result.user.uid,
              loginUser: result.user.displayName,
              profileImg: result.user.photoURL,
            },
          });
        })
        .catch((error) => {
          alert(error.message);
        });
    },
  },
};
</script>

<style scoped></style>
