<template>
  <div class="roomVue">
    <div class="rooms">
      <aside>
        <div class="aside-head">
          <h2>Rooms</h2>
          <p>
            <span id="mark">●</span>
            {{ loginUser }}さん
          </p>
        </div>
        <div class="aside-body">
          <section>
            <div
              v-for="(menu, index) in menuList"
              @click="activeItem = index"
              v-bind:key="menu.number"
              v-bind:class="{ clicked: activeItem === index }"
            >{{ menu.name }}</div>
          </section>
        </div>
      </aside>

      <main>
        <div class="main-head">
          <template v-if="activeItem !== ''">
            <h2>{{ menuList[activeItem].name }}</h2>
            <div>
              <b-form-group>
                <b-form-radio-group
                  id="btn-radios-1"
                  v-model="selected"
                  :options="options"
                  buttons
                  name="radios-btn-default"
                ></b-form-radio-group>
              </b-form-group>
            </div>
          </template>
        </div>
        <div class="main-body">
          <!-- Room作成タブ -->
          <roomCreation
            v-show="activeItem == 0"
            v-bind:errorMsg="this.errorMsg"
            v-bind:loginUser="this.loginUser"
            v-bind:userId="this.userId"
          ></roomCreation>

          <!-- Room一覧タブ -->
          <roomList
            v-show="activeItem == 1"
            v-bind:filteredRooms="this.filteredRooms"
            v-on:enterRoom="enterRoom"
            v-on:chageFavorites="chageFavorites"
          ></roomList>

          <!-- My Roomタブ -->
          <myRoom
            v-show="activeItem == 2"
            v-bind:filteredRooms="this.filteredRooms"
            v-bind:userId="this.userId"
            v-on:enterRoom="enterRoom"
            v-on:chageFavorites="chageFavorites"
            v-on:deleteRoom="deleteRoom"
          ></myRoom>

          <!-- favoritesタブ -->
          <favRoom
            v-show="activeItem == 3"
            v-bind:filteredRooms="this.filteredRooms"
            v-on:enterRoom="enterRoom"
            v-on:chageFavorites="chageFavorites"
          ></favRoom>

          <!-- chatbot -->
          <section v-show="activeItem == 4" class="chatbot">
            <chatbot v-bind:loginUser="loginUser" v-bind:profileImg="profileImg"></chatbot>
          </section>

          <!-- RoomKey入力フォーム -->
          <section id="modal" v-bind:class="{ hidden: !isOpenModal }">
            <form @submit.prevent="enterRoomForPrivateChat">
              <div class="form-group">
                <div v-show="this.errorMsg !== ''" style="color: red">{{ errorMsg }}</div>
                <label type="password">ルームキーを入力してください：</label>
                <input
                  type="password"
                  class="form-control"
                  id="password"
                  v-model="inputKey"
                  ref="focusThis"
                />
              </div>
            </form>
            <div id="close" @click="isOpenModal = !isOpenModal">閉じる</div>
          </section>
          <div id="mask" v-bind:class="{ hidden: !isOpenModal }"></div>
        </div>
      </main>
    </div>
    <footer></footer>
  </div>
</template>

<script>
import firebase from "firebase";
import mixin from "../mixins.js";
import chatbot from "./chat/chatbot.vue";
import roomList from "./room/roomList.vue";
import myRoom from "./room/myRoom.vue";
import favRoom from "./room/favRoom.vue";
import roomCreation from "./room/roomCreation.vue";

export default {
  name: "room",
  props: [],
  components: {
    chatbot: chatbot,
    roomList: roomList,
    myRoom: myRoom,
    favRoom: favRoom,
    roomCreation: roomCreation,
  },
  data: function () {
    return {
      rooms: [],
      fav_rooms: [],
      menuList: [
        {
          number: 0,
          name: "Room作成",
        },
        {
          number: 1,
          name: "Room一覧",
        },
        {
          number: 2,
          name: "My Room",
        },
        {
          number: 3,
          name: "Favorites",
        },
        {
          number: 4,
          name: "Question To Chang",
        },
      ],
      isOpenModal: false,
      // showRoomCreationFormFlag: false,
      inputKey: "",
      roomKey: "",
      errorMsg: "",
      loginUser: this.$route.query.loginUser,

      currentRoom: "",
      activeItem: 1,
      userId: this.$route.query.userId,
      profileImg: this.$route.query.profileImg,
      fav_roomIds: "",
      options: [
        { text: "All", value: 0 },
        { text: "Private", value: 1 },
        { text: "Open", value: 2 },
      ],
      selected: [0],
      boforeSortRooms: [],
    };
  },
  mixins: [mixin],

  created: function () {
    //初期化処理
    this.isOpenModal = false;
    this.roomKey = "";
    this.errorMsg = "";
    //rooms取得
    this.getRoom();
  },

  methods: {
    /**
     * Cloud Firestoreへ接続しroomオブジェクトを取得する
     */
    getRoom: function () {
      let rooms = [];

      //ルームリスト取得
      let promise = new Promise((resolve) => {
        firebase
          .firestore()
          .collection("rooms")
          .orderBy("last_modified")
          .onSnapshot((snap) => {
            snap.docChanges().forEach((change) => {
              if (change.type === "added") {
                const room = change.doc.data();
                room.addFlg = false;
                rooms.push(room);
                console.log("room取得しました");
              }
            });
          });
        resolve();
      });
      //TODO
      //最終更新日でソート
      promise.then(() => {
        setTimeout(() => {
          this.rooms = rooms.sort(function (a, b) {
            return a.last_modified < b.last_modified ? 1 : -1;
          });
          console.log("ソートしました");
          console.log(this.rooms);
        }, 500);
      });
    },

    /**
     * Cloud Firestoreへ接続しお気に入りroomIdを取得
     * それをもとにroomオブジェクトにaddFlgを追加
     */
    setFavRooms: function () {
      //お気に入りルームリスト取得
      let fav_roomIds = [];

      //お気に入りに登録されている部屋のidを取得する
      firebase
        .firestore()
        .collection("favorites")
        .doc(this.userId)
        .get()
        .then((doc) => {
          if (doc.exists) {
            fav_roomIds = doc.data().fav_roomIds;
            console.log("fav_roomIds取得成功");
            console.log(fav_roomIds);

            //お気に入り登録判定に使用するためdataに持たせておく
            this.fav_roomIds = fav_roomIds;
          } else {
            console.log("fav_roomsが１つもありません");
            return;
          }
        })
        .catch((error) => {
          console.log(error.message);
          return;
        });

      //fav_roomIdsを参照して、roomsのお気に入りフラグをtrueにする
      setTimeout(() => {
        this.rooms.forEach((room) => {
          if (this.fav_roomIds.includes(room.roomId)) {
            room.addFlg = true;
          }
        });
        console.log("お気に入りroomセット完了");
      }, 500);
    },

    /**
     * クリックしたroomの属性によって処理を振り分けるメソッド
     * openChatの場合：sendToChat
     * privateChatの場合：openModal
     * @param room オブジェクト
     */
    enterRoom: function (room) {
      //openChatの場合
      if (room.roomKey === "") {
        this.currentRoom = room;
        this.sendToChat(room);
        return;
      }

      //privateChatの場合
      this.openModal(room);
    },

    /**
     * chatへ画面遷移する
     * 引継ぎ情報：userId,loginUser,profileImg,roomId
     */
    sendToChat: function () {
      this.$router.push({
        path: "/chat",
        query: {
          userId: this.userId,
          loginUser: this.loginUser,
          profileImg: this.profileImg,
          roomId: this.currentRoom.roomId,
        },
      });
    },

    /**
     * chatへ画面遷移する(privateChatの場合)
     * 引継ぎ情報：userId,loginUser,profileImg,roomId
     */
    enterRoomForPrivateChat: function () {
      //TODO
      let promise = new Promise((resolve) => {
        this.errorMsg = "";
        resolve();
      });

      promise.then(() => {
        //入室前に鍵があるかどうか、あった場合はそれが正しい鍵かをチェック
        if (this.roomKey !== "" && !this.checkRoomKey()) {
          setTimeout(() => {
            this.errorMsg = "ルームキーが違います";
          }, 600);
          return;
        } else {
          this.sendToChat();
        }
      });
    },

    /**
     *入力されたroomkeyのチェック処理を行う
     */
    checkRoomKey: function () {
      if (this.inputKey !== this.roomKey) {
        return false;
      }
      this.roomKey = "";
      return true;
    },

    /**
     *roomKey入力フォームを開く
     * @param room オブジェクト
     */
    openModal: function (room) {
      //roomKeyをセット
      this.currentRoom = room;
      this.roomKey = room.roomKey;

      this.errorMsg = "";

      //roomオブジェクトにroomKeyがない場合システムエラー
      if (this.roomKey === "") {
        alert("system error");
        return;
      }

      //モーダルを開く
      this.isOpenModal = !this.isOpenModal;
      //フォーカスをあてる
      this.$refs.focusThis.focus();
      //上スクロール
      this.scrollUp();
    },

    /**
     * 上スクロール
     */
    scrollUp: function () {
      window.scrollTo(0, 0);
    },

    /**
     * お気に入りの追加または削除を行う
     * @param event イベントオブジェクト
     * @param room roomオブジェクト
     */
    chageFavorites: function (event, room) {
      //親要素へtのクリックイベント伝番を防ぐ
      event.stopPropagation();

      //クリックしたroomの表示切替
      room.addFlg = !room.addFlg;

      //Cloud FireStoreへ接続しお気に入りを更新
      this.updateFavorites();
    },

    /**
     * Cloud Firestoreへ接続し
     * お気に入りroomのroomIdを更新する
     */
    updateFavorites: function () {
      let fav_rooms = [];

      //DB登録のためお気に入りroomIdだけの配列を作成する
      //1,お気に入りroomリストを作成
      fav_rooms = this.rooms.filter((room) => {
        return room.addFlg === true;
      });
      //2,roomIdだけを切り出す
      this.fav_roomIds = fav_rooms.map((room) => {
        return room.roomId;
      });

      //Cloud Firestoreに登録
      firebase
        .firestore()
        .collection("favorites")
        .doc(this.userId)
        .set({
          fav_roomIds: this.fav_roomIds,
          uid: this.userId,
          useName: this.loginUser,
        })
        .then(() => {
          console.log(`update favorites!`);
        })
        .catch((error) => {
          console.log(error);
        });
    },

    /**
     * roomを削除する
     * @param event イベントオブジェクト
     * @param room roomオブジェクト
     */
    deleteRoom: function (event, room) {
      //イベントが親要素へ伝番しないようにする
      event.stopPropagation();

      if (!window.confirm(`${room.roomName}を削除してもよろしいですか？`)) {
        return;
      }

      //room削除処理
      firebase
        .firestore()
        .collection("rooms")
        .doc(room.roomId)
        .delete()

        .then(function () {
          console.log(`${room.roomName} delteed!`);
        })
        .catch((error) => {
          console.log(error);
        });

      //画面を更新
      this.getRoom();
      this.setFavRooms();
    },
  },

  watch: {
    rooms: function () {
      //お気に入りを再設定
      this.setFavRooms();

      setTimeout(() => {
        this.selected = 0;
      }, 900);
    },
  },

  computed: {
    filteredRooms: function () {
      let filteredRooms = [];

      //allの場合
      if (this.selected === 0) {
        return this.rooms;
      }
      //privateの場合
      if (this.selected === 1) {
        this.rooms.forEach((room) => {
          if (room.roomKey !== "") {
            filteredRooms.push(room);
          }
        });
      }
      //openの場合
      if (this.selected === 2) {
        this.rooms.forEach((room) => {
          if (room.roomKey === "") {
            filteredRooms.push(room);
          }
        });
      }

      return filteredRooms;
    },
  },
};
</script>

